export default [
    {
        path: "/CSS",
        name: "css",
        component: () => import("@/pages/CSS/css"),
        meta: { title: "CSS" }
    },
    {
        path: "/CSS/基础教程/教程",
        name: "CssBasicCourses",
        component: () => import("@/pages/CSS/basicCourses/courses"),
        meta: { title: "CSS 教程" }
    }
];



